const Noticia = (props) => {

    const {urltoImage, url, title, description, source} = props.noticia;

    let imagen;

    if(urltoImage != '') {
        imagen = <img src={urltoImage} alt={title}
        className="card-img-top" />
    }

    return ( 
        <div className="col-md-6 col-12 mg-4">
            <div className="card">
                <div className="card-image">
                    {imagen}
                </div>
            </div>
        </div>
     );
}
 
export default Noticia;